body{
  color: #eee;
}

a {
  text-decoration: none;
}

a:link {
  color: #cad;
}

a:visited {
  color: #cad;
}

a:hover {
  color: rgb(198, 236, 255);
  text-decoration:wavy;
}

a:active {
  color: #bcf;
}

ul{
  list-style: none;
}

li{
  padding: 0 0 0.5rem 0;
}

#hero{
  position: fixed;
  z-index: 8;
  width: 100vw;
  background-color: #333;
  display: grid;
  grid-template-columns: 12rem 1fr 2fr 2fr;
  gap: 3rem;
}

.nav-bar{
  font-size: 1.2em;
  max-width: 500px;
  padding: 0.5rem 0rem 0rem 0rem;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  text-align: center;
  gap: 0rem;
}

.nav-link{
  padding: 0 1rem 0 1rem;
}

.burger {
  display: none;
}

#hero h1{
  padding: 0rem 2rem 0rem 2rem;
  font-size: 2em;
}

.content{
  margin: 0 auto;
  max-width: 1200px;
  padding: 6rem 2rem 3rem 2rem;
}

h3 {
  margin-top:0;
}

#elliot-mb{
  color: #eee;
}

/* .body{
  min-height: 100vh;
} */

.footer{
  margin-top: 2.5rem;
}

